<template>
    <v-container style="position: relative;">
        <v-app-bar
                fixed
                app
                color="#FFFFFF"
                elevation="3"
        >
            <v-row >
                <v-col></v-col>
                <v-col class="text-center"><v-toolbar-title>{{yorProfileText}}</v-toolbar-title></v-col>
                <v-col class="text-right" style="padding-top: 0; padding-bottom: 0"><v-btn class="app-bar-button" small tile style="display: inline;" text color="primary"
                              @click="openProfileEdit">{{editText}}
                </v-btn></v-col>
            </v-row>


        </v-app-bar>
        <v-row style="margin-top: 45px">
            <PublicProfile
                    :profile-image="profileImage"
                    :header-image="headerImage"
                    :username="username"
                    :known-languages="knownLanguages"
                    :languages-to-learn="languagesToLearn"
                    :description="description"
                    :birth-date="birthDate"
                    :avg-rating="avgRating"
                    :num-ratings="numRatings"
                    :my-rating-is-visible="false"

            >
            </PublicProfile>
        </v-row>

        <v-row>
            <v-col cols="12">
                <v-container class="bottom-centered-buttons">
                    <v-btn block color="primary" @click="wantToLogout">Cerrar sesión</v-btn>
                </v-container>
            </v-col>
        </v-row>

        <v-dialog
                v-model="dialog"
        >
            <v-card>
                <br>
                <v-card-text align="justify" style="color: black; padding-bottom: 0px;">
                    <h3 style="margin-bottom: 10px">Va a salir de su cuenta</h3>
                    <p style="margin-bottom: 7px">¿Está seguro de que quiere cerrar sesión?</p>
                </v-card-text>

                <v-card-actions style="padding-bottom: 15px">
                    <v-spacer></v-spacer>
                    <v-btn color="primary" @click="dialog = false">Cancelar</v-btn>
                    <v-btn color="primary" @click="logout">Confirmar</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>

        <v-snackbar
                v-model="snackbar"
                :timeout="4000"
        >
            {{ snackbarText }}
            <v-btn
                    color="blue"
                    text
                    @click="snackbar = false"
            >
                {{closeText}}
            </v-btn>
        </v-snackbar>
    </v-container>

</template>

<script>
    import PublicProfile from "./PublicProfile";
    import {mapActions} from 'vuex';
    import Constants from "../../store/Constants";

    export default {
        name: "ProfileHome",
        data: () => ({
            dialog: false,
            profileImage: null,
            headerImage: null,
            username: null,
            knownLanguages: null,
            birthDate: null,
            languagesToLearn: null,
            snackbar: false,
            snackbarText: null,
            description: null,
            avgRating: null,
            numRatings: null,
            yorProfileText: Constants.TEXT_YOUR_PROFILE,
            editText: Constants.TEXT_EDIT,
            closeText: Constants.TEXT_CLOSE
        }),
        methods: {
            ...mapActions([
                'logoutUserFirebase',
                'getUserInfo',
            ]),
            wantToLogout() {
                let self = this;
                self.dialog = true;
            },
            logout() {
                let resultLogoutUserFirebase = this.logoutUserFirebase();

                resultLogoutUserFirebase.then(() => {
                    this.$router.push('home');
                })
                    .catch((error) => {
                        this.snackbarText = error;
                        this.snackbar = true;
                    });
            },
            openProfileEdit() {
                this.$router.push({name: 'profileEdit', params: {id: this.$route.params.id}})
            },
            getUserInfo() {
                let userInfo = this.$store.getters.getUserInfo;

                this.profileImage = userInfo.profileImage;
                this.username = userInfo.username;
                this.description = userInfo.description;
                this.birthDate = this.$store.getters.getFormattedDateInTextMonth(userInfo.birthDate.toDate());
                this.knownLanguages = userInfo.knownLanguages;
                this.languagesToLearn = userInfo.languagesToLearn;
                this.avgRating = userInfo.avgRating;
                this.numRatings = userInfo.numRatings;
                this.headerImage = this.$store.getters.getProfileHeaderImage;
            },
        },
        mounted() {
            this.getUserInfo();
        },
        components: {
            PublicProfile
        }
    }
</script>

<style scoped>
    .bottom-centered-buttons {
        width: 92.5%;
        left: 50%;
        transform: translate(-50%, 0);
        position: absolute;
    }

    .app-bar-button {
        height: 100% !important;
        min-width: unset !important;
    }
</style>
